import * as React from 'react';
import { ItemKey, ItemKeys, ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';
import FontButton, { FontsButtonProps } from './components/next/fontsButton';

const SMALL_WIDTH = 70;
const WIDE_WIDTH = 135;

const buttons: Partial<Record<ItemKeys, React.ComponentType<FontsButtonProps>>> = {
  'fonts': FontButton,
  'fonts.wide': FontButton
};

export default function createRenderToolbar(
  locale: Record<string, string> = {},
): RenderToolbar {


  return (props, controller) => {

    const { buttonName, extraProps } = props;

    const Component = buttons[buttonName] as React.ComponentType<FontsButtonProps>;
    if (Component) {

      const width = buttonName === ItemKey.fonts ? SMALL_WIDTH : WIDE_WIDTH;
      return (
        <ItemSlot display={SlotType.textSelect} width={width}>
          <Component
            controller={controller}
            locale={locale}
            width={width}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    return null
  };
}
